<template>
    <!-- 通用页头 -->
    <div class="header">
        <div class="centerbox">
            <h1 class="logo"><img src="static/images/logo.png" width="162" height="48" alt=""/></h1>
            <h2 class="title">电量应用系统</h2>
            <div class="now_time">{{date}}</div>
            
            <!--导航-->
            <ul class="nav">
                <li :class="{selected:name=='yxzl'}" @click="toJumpt('yxzl')"><a ><i class="yun_nav_icon_yxzl"></i>运行总览</a></li>
                <li :class="{selected:name=='mxph'}" @click="toJumpt('mxph')"><a ><i class="yun_nav_icon_mxph"></i>母线平衡</a></li>
                <li :class="{selected:name=='fbxs'}" @click="toJumpt('fbxs')"><a ><i class="yun_nav_icon_fbxs"></i>分布线损</a></li>
                <li :class="{selected:name=='dlcx'}" @click="toJumpt('dlcx')"><a ><i class="yun_nav_icon_dlcx"></i>电量查询</a></li>
            </ul>
            <div style="clear:both;"></div>
        </div>
    </div>
</template>
<script>
export default {
    data:function(){
        return{
            name:'yxzl',
            date:''
        }
    },
    mounted(){
        console.log(this.$route)
        this.date=this.$getDate('day',new Date());
        this.name=this.$route.name;
    },
    methods:{
        toJumpt(name) {
            this.name=name;
            var { href } = this.$router.resolve({
            path: "/"+name,
            });
            window.open(href, "self");
        },
    }
}
</script>
<style lang="css">
    .testCheck{
        color:blue;
    }
</style>


