<template>
  <dl class="listIndex">
    <dt>{{name}}</dt>
    <dd v-if="name!=='统计对象'" style="border-bottom:#45cce4 1px dashed;">
      <span
        :class="{checkPingy:checkPingy==item}"
        @click="changeCheckPy(item)"
        class="pingy"
        v-for="(item,index) in pingyin"
        :key="index"
      >{{item}}</span>
    </dd>
    <dd>
      <label v-if="hasDuos">
        <a @click="checkAll">所有</a>
      </label>
      <!-- <label v-else>
        <a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</a>
      </label>-->
      <template v-if="duox">
        <label :key="index" v-for="(item,index) in data1">
          <input v-model="check1" :value="item" type="checkbox">
          <a>{{item}}</a>
        </label>
      </template>
      <template v-else>
        <label :key="index" v-for="(item,index) in data1">
          <input :name="name+'r'" v-model="check2" :value="item" type="radio">
          <a>{{item}}</a>
        </label>
      </template>
    </dd>
    <span
      v-if="hasDuos"
      style="float:right;display:inline-block;text-align:left;border:1px solid #ddd;border-radius:5px;"
    >
      <label style="width:100%;line-height:20px;margin:0 5px;">
        <input v-model="duox" @click="checkDuox" style="margin-left:5px;" type="checkbox" value>多选
      </label>
    </span>
    <div v-if="data2.length>0" class="xiabiaobox f_r">
      <div @click="xiabiao=!xiabiao" class="xiabiao">更多</div>
      <el-scrollbar v-show="xiabiao" class="xiabiaoul" style="width:100px;height:200px;">
        <template v-if="duox">
          <label :key="index" v-for="(item,index) in data2" style="width:100%;display:inline-flex;">
            <input v-model="check1" :value="item" type="checkbox">
            <a>{{item}}</a>
          </label>
        </template>
        <template v-else>
          <label :key="index" v-for="(item,index) in data2" style="width:100%;display:inline-flex;">
            <input :name="name+'r'" v-model="check2" :value="item" type="radio">
            <a>{{item}}</a>
          </label>
        </template>
        <div class="jt"></div>
      </el-scrollbar>
    </div>
    <div style="clear:both"></div>
  </dl>
</template>
<script>
import { pingyingPipei_ } from "@/assets/js/pingyinPipei.js";
export default {
  props: ["num", "name", "data", "hasDuos"],
  data: function() {
    return {
      duox: false,
      data1: [],
      data2: [],
      check1: [],
      check2: "",
      xiabiao: false,
      pingyin: [
        "#",
        "a",
        "b",
        "c",
        "d",
        "e",
        "f",
        "g",
        "h",
        "i",
        "j",
        "k",
        "l",
        "m",
        "n",
        "o",
        "p",
        "q",
        "r",
        "s",
        "t",
        "u",
        "v",
        "w",
        "x",
        "y",
        "z"
      ],
      checkPingy: ""
    };
  },
  computed: {
    check: function() {
      if (this.duox) {
        return this.check1;
      } else {
        if (this.check2 == "") {
          return [];
        } else {
          return [this.check2];
        }
      }
    }
  },
  mounted() {
    if (this.data && this.data.length > 7) {
      console.log("come");
      for (var i = 0; i < 7; i++) {
        this.data1.push(this.data[i]);
      }
      for (var i = 7; i < this.data.length; i++) {
        this.data2.push(this.data[i]);
      }
      console.log(this.data2);
    } else {
      this.data1 = this.data;
      this.data2 = [];
    }
    this.check2 = this.data[0];
  },
  methods: {
    checkAll() {
      this.duox = false;
      this.check1 = [];
      this.check2 = "";
      this.data1 = [];
      this.data2 = [];
      if (this.data && this.data.length > 7) {
        console.log("come");
        for (var i = 0; i < 7; i++) {
          this.data1.push(this.data[i]);
        }
        for (var i = 7; i < this.data.length; i++) {
          this.data2.push(this.data[i]);
        }
        console.log(this.data2);
      } else {
        this.data1 = this.data;
        this.data2 = [];
      }
      this.check2 = this.data[0];
      this.checkPingy = "";
    },
    checkDuox() {
      this.check1 = [];
      this.check2 = "";
    },
    updatedCheck() {
      setTimeout(() => {
        this.$emit("changeCheck", this.num, this.check);
      }, 1);
    },
    delCheck(index) {
      if (this.duox) {
        this.check1.splice(index, 1);
      } else {
        this.check2 = "";
      }
    },
    delCheckAll() {
      this.check1 = [];
      this.check2 = "";
      this.updatedCheck();
    },
    changeCheckPy(item) {
      this.checkPingy = item;
      this.pingyinPipei();
    },
    //根据首字母过滤
    pingyinPipei() {
      this.check1 = [];
      this.check2 = "";
      this.data1 = [];
      this.data2 = [];
      var arr = pingyingPipei_(this.data, this.checkPingy);
      if (arr && arr.length > 7) {
        console.log("come");
        for (var i = 0; i < 7; i++) {
          this.data1.push(arr[i]);
        }
        for (var i = 7; i < arr.length; i++) {
          this.data2.push(arr[i]);
        }
        console.log(this.data2);
      } else {
        this.data1 = arr;
        this.data2 = [];
      }
      if (this.data1.length > 0) {
        if (this.duox) {
          this.check1 = [this.data1[0]];
        } else {
          this.check2 = this.data1[0];
        }
      }
    }
  },
  watch: {
    check: function() {
      this.updatedCheck();
    },
    duox: function() {
      if (this.duox) {
        this.check1 = [this.data[0]];
      } else {
        this.check2 = this.data[0];
      }
    }
  }
};
</script>
<style>
.screenBox dl dd {
  width: 937px;
}
.screenBox dl {
  overflow: visible;
}
.listIndex .pingy {
  width: 34px;
  font-size: 14px;
  text-decoration: underline;
  display: inline-block;
  color: #45cce4;
  cursor: pointer;
}
.listIndex .pingy:hover {
  background: #9bbfc5;
  color: #fff;
}
.listIndex .checkPingy {
  background: #45cce4;
  color: #fff;
}
</style>



