function getDate_(type,date,addMonth){
    //获取指定日期类型
    var year=date.getFullYear();
    var month=date.getMonth()+1;
    if(addMonth){
        month+=addMonth;
    }
        month=month<10?'0'+month:month;
    var day=date.getDate();
        day=day<10?'0'+day:day;
    if(type=="month"){
        return year+'-'+month;
    }else if(type=="day"){
        return year+'-'+month+'-'+day;
    }else if(type=='year'){
        return year;
    }else{
        return '';
    }
}
export {getDate_}


