import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import hnIndex from '@/components/index/index'
import yxzl from '@/components/yxzl/index'
import mxph from '@/components/mxph/index'
import fbxs from '@/components/fbxs/index'
import dlcx from '@/components/dlcx/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'hnIndex',
      component: hnIndex,
      children:[
        {
          path:'/',
          name:'yxzl',
          component:yxzl
        },
      ]
    },
    {
      path: '/index',
      component: hnIndex,
      children:[
        {
          path:'/',
          name:'yxzl',
          component:yxzl
        },
        {
          path:'/yxzl',
          name:'yxzl',
          component:yxzl
        },
        {
          path:'/mxph',
          name:'mxph',
          component:mxph
        },
        {
          path:'/fbxs',
          name:'fbxs',
          component:fbxs
        },
        {
          path:'/dlcx',
          name:'dlcx',
          component:dlcx
        }
      ]
    }
  ]
})



