<template>
  <div class="centerbox fbxs">
    <div class="modularbox m_t" style="padding:0 0 20px;height:400px">
      <!-- 日数据 -->
      <div id="dayData" style="width:450px;height:100%;float:left;"></div>
      <div style="float:right;width:calc(100% - 450px);box-sizing:border-box;padding:10px;">
        <div style="text-align:right;">
          <span>日数据：</span>
          <el-date-picker
            type="date"
             @change="inquireOne"
            v-model="date1"
            value-format="yyyy-MM-dd"
            placeholder="选择一个日期"
          ></el-date-picker>
          <el-select @change="inquireOne" v-model="dayType1" placeholder="请选择">
            <el-option v-for="item in ['线路选择','线路选择1']" :key="item" :label="item" :value="item"></el-option>
          </el-select>
          <button @click="inquireOne" class="layui-btn cxbutt">查询</button>
        </div>
        <el-table
          border
          :data="riData"
          show-summary
          :summary-method="summaryMethod"
          :stripe="true"
          height="350"
        >
          <el-table-column prop="name" label="设备名称" fixed></el-table-column>
          <el-table-column label="数据质量" width="50"></el-table-column>
          <el-table-column prop="input1" label="输入电量起始底码"></el-table-column>
          <el-table-column prop="input2" label="输入电量终止底码"></el-table-column>
          <el-table-column prop="input3" label="输入电量  (kWh)" sortable width="90"></el-table-column>
          <el-table-column prop="output1" label="输出电量起始底码"></el-table-column>
          <el-table-column prop="output2" label="输出电量终止底码"></el-table-column>
          <el-table-column prop="output3" label="输出电量  (kWh)" sortable width="90"></el-table-column>
          <el-table-column prop="ct" label="CT" width="70"></el-table-column>
          <el-table-column prop="pt" label="PT" width="70"></el-table-column>
          <el-table-column prop="ratio" label="倍率" width="70"></el-table-column>
          <el-table-column prop="type" label="接线方式" width="70"></el-table-column>
        </el-table>
      </div>
    </div>
    <div class="modularbox m_t" style="padding:0 0 20px;height:400px">
      <!-- 月数据 -->
      <div id="monthData" style="width:450px;height:100%;float:left;"></div>
      <div style="float:right;width:calc(100% - 450px);box-sizing:border-box;padding:10px;">
        <div style="text-align:right;">
          <span>月数据：</span>
          <el-date-picker @change="inquireTwo" type="month" v-model="date2" value-format="yyyy-MM" placeholder="选择一个日期"></el-date-picker>
          <el-select @change="inquireTwo" v-model="dayType2" placeholder="请选择">
            <el-option v-for="item in ['线路选择','线路选择1']" :key="item" :label="item" :value="item"></el-option>
          </el-select>
          <button @click="inquireTwo" class="layui-btn cxbutt">查询</button>
        </div>
        <el-table border :data="riData" height="350" style="width:100%">
          <el-table-column prop="name" label="设备名称" fixed></el-table-column>
          <el-table-column label="数据质量" width="50"></el-table-column>
          <el-table-column prop="input1" label="输入电量起始底码"></el-table-column>
          <el-table-column prop="input2" label="输入电量终止底码"></el-table-column>
          <el-table-column prop="input3" label="输入电量  (kWh)" sortable width="90"></el-table-column>
          <el-table-column prop="output1" label="输出电量起始底码"></el-table-column>
          <el-table-column prop="output2" label="输出电量终止底码"></el-table-column>
          <el-table-column prop="output3" label="输出电量  (kWh)" sortable width="90"></el-table-column>
          <el-table-column prop="ct" label="CT" width="70"></el-table-column>
          <el-table-column prop="pt" label="PT" width="70"></el-table-column>
          <el-table-column prop="ratio" label="倍率" width="70"></el-table-column>
          <el-table-column prop="type" label="接线方式" width="70"></el-table-column>
        </el-table>
      </div>
    </div>
    <div style="clear:both"></div>
  </div>
</template>
<script>
export default {
  data: function() {
    return {
      date1: "",
      date2: "",
      dayType1: "",
      dayType2: "",
      loading: true,
      riData: []
    };
  },
  mounted() {
    this.date1 = this.$getDate("day", new Date());
    this.date2 = this.$getDate("month", new Date());
    this.getDayData("dayData");
    this.getDayData("monthData");
    for (var i = 0; i < 10; i++) {
      this.riData.push({
        name: "城旺2M09线",
        input1: 4982.326,
        input2: 4982.809,
        input3: 531300,
        output1: 57.928,
        output2: 57.928,
        output3: 0,
        ct: "2500/5",
        pt: "220/0.1",
        ratio: 110000,
        type: "反接线"
      });
    }
  },
  methods: {
    getDayData(type) {
      var dayData = echarts.init(document.getElementById(type), "yundw");
      var option = {
        tooltip: {
          trigger: "axis"
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"]
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            data: [820, 932, 901, 934, 1290, 1330, 1320],
            type: "line",
            areaStyle: {}
          }
        ]
      };
      dayData.setOption(option);
    },
    //自定义合计
    summaryMethod({ columns, data }) {
      // return 112;
      var sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "合计";
        } else if (index === 4) {
          var sum = 0;
          for (var a of this.riData) {
            if (a.input3 && parseFloat(a.input3)) {
              sum += parseFloat(a.input3);
            }
          }
          sums[index] = sum;
        }else if(index ===7){
          sum=0;
          for(var a of this.riData){
            if (a.output3 && parseFloat(a.output3)) {
              sum += parseFloat(a.output3);
            }
          }
          sums[index] = sum;
        }
      });
      return sums;
    },
    //查询1 日查询
    inquireOne(){
      console.log('日查询',this.date1,this.dayType1);
    },
    //查询2 月查询
    inquireTwo(){
      console.log('月查询',this.date2,this.dayType2);
    }
  }
};
</script>
<style>
.fbxs {
  padding: 0 0 20px;
}
.fbxs input {
  height: 30px !important;
  width: 140px !important;
}
.fbxs .el-date-editor.el-input,
.fbxs .el-date-editor.el-input__inner {
  width: 140px !important;
}
.fbxs .cxbutt {
  margin: 0;
}
.fbxs .el-input__icon {
  line-height: 30px;
}
.fbxs .el-table__fixed-header-wrapper th {
  background-color: #11b8ae;
  color: #fff;
  border-right: none;
  text-align: center;
}
.fbxs .el-table__header-wrapper th {
  background-color: #11b8ae;
  color: #fff;
  border-right: none;
  text-align: center;
}
.fbxs .el-table th > .cell {
  letter-spacing: 0px;
  line-height: 14px;
}
.fbxs .el-table .caret-wrapper {
  height: 17px;
}
.fbxs .el-table .sort-caret.ascending {
  top: -2px;
}
.fbxs .el-table .sort-caret.descending {
  top: 10px;
}
</style>



