<template>
  <!-- 首页 运行总览 -->
  <div>
    <t-header></t-header>
    <router-view class="main"/>
    <!-- <t-footer></t-footer> -->
  </div>
</template>
<script>
import tHeader from "./header";
import tFooter from "./footer";
export default {
  components: { tHeader, tFooter }
};
</script>
<style lang="css">
.main {
  min-height: calc(100vh - 90px);
  width: 1240px;
  margin: 0 auto;
}
.main .el-table .cell {
  line-height: 14px;
}
.main .el-table td,
.main .el-table th {
  padding: 5px 0;
}
.main .el-input__inner{
  height:31px;
}
.main .el-date-editor .el-range__icon,.main .el-date-editor .el-range-separator{
  line-height:23px;
}
.main .el-date-editor--daterange input{
  height:29px;
}
/* 选择数据类型的下标 */
.xiabiaobox{
  position:relative;
  cursor:default;
  display:inline-block;
  vertical-align: top;
}
.xiabiaobox input{
  vertical-align: middle;
}
.xiabiao{
  line-height: 10px;border:1px solid #ddd;
  width:46px;padding:5px;border-radius:5px;position:relative;
}
.xiabiaoul li{
  margin-bottom:5px;
  box-sizing: border-box;
  border:none !important;
  padding:0 !important;
}
.xiabiao::after{
  content: '';
  width:0;
  height:0;
border-width:5px;border-color:#0D7474 transparent transparent transparent;border-style:solid;
position:absolute;right:5px;top:8px;
}
.xiabiaoul{
  position:absolute;
  top:27px;
  z-index:40;
  background-color: #fff;
  padding:5px;
  border:1px solid #e4e7ed;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
}
.xiabiaoul .jt{
  content: '';
  display:block;
  position:absolute;
  border-width:8px;
  border-color:transparent;
  border-style: solid;
  border-bottom-color:#ddd;
  border-top-width:0;
  top:-8px;
}
.xiabiaoul .jt::before{
  content: '';
  display:block;
  position:absolute;
  border-width:6px;
  border-color:transparent;
  border-style: solid;
  border-bottom-color:#fff;
  border-top-width:0;
  top:2px;
  left:-6px;
}
/* 选择数据类型的下标 */
</style>


