<template>
  <div class="centerbox yxzl">
    <div class="lan08 f_l">
      <div class="modularbox no_p_b m_t">
        <!--母线平衡-->
        <div class="mknkd50 f_l yxzl_gd">
          <h2 class="modular_title">母线平衡</h2>
          <div class="hglpin" id="yxzl_mxhgl_pin">
            <!--实时合格率-->
          </div>
          <div class="hglzx" id="yxzl_mxhgl_zx">
            <!--母线平衡合格率折线-->
          </div>
        </div>
        <!--母线平衡END-->
        <!--分线线损-->
        <div class="mknkd50 f_r">
          <h2 class="modular_title">分线线损</h2>
          <div class="hglpin" id="yxzl_fxxs_pin">
            <!--实时合格率-->
          </div>
          <div class="hglzx" id="yxzl_fxxs_zx">
            <!--母线平衡合格率折线-->
          </div>
        </div>
        <!--分线线损END-->
      </div>
    </div>

    <!--全网日电量统计-->
    <div class="lan04 f_l m_t">
      <div class="modularbox p_b_10">
        <h2 class="modular_title">全网日电量统计
          <span class="f_r dwbz">(单位:亿千瓦时)</span>
        </h2>
        <div class="sxbtbox" id="day_dltj"></div>
      </div>
    </div>

    <!--地区日电量统计-->
    <div class="lan04 f_r m_t">
      <div class="modularbox p_b_10">
        <h2 class="modular_title">地区日电量统计
          <span class="f_r dwbz">(单位:亿千瓦时)</span>
        </h2>
        <div class="sxbtbox" id="map_dltj"></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
    mounted(){
        this.getEcharts1('实时合格率','yxzl_mxhgl_pin','#3ab5a0');
        this.getEcharts1('实时合格率','yxzl_fxxs_pin','#277ee5');
        this.getEcharts2('历史合格率','yxzl_mxhgl_zx',"#3ab5a0",'rgba(58,181,160, 0.5)');
        this.getEcharts2('历史合格率','yxzl_fxxs_zx',"#277ee5",'rgba(39,126,229, 0.5)');
        var data1=
        [
            ['10-1', '10-3', '10-5', '10-7', '10-9', '10-11', '10-13', '10-15', '10-17', '10-19', '10-21', '10-23', '10-25', '10-27', '10-29', '10-31'],
            [389, 259, 262, 324, 232, 176, 196, 214, 133, 370, 268, 360, 185, 392, 392, 153],
            [121, 388, 233, 309, 133, 308, 297, 283, 349, 273, 229, 238, 224, 291, 185, 203]
        ];
        this.getEcharts3('全网日电量统计','day_dltj',data1);
        var data2=
        [
            ['长沙', '株洲', '湘潭', '衡阳', '邵阳', '岳阳', '常德', '张家界', '益阳', '娄底'],
            [389, 259, 262, 324, 232, 176, 196, 214, 133, 370],
            [121, 388, 233, 309, 133, 308, 297, 283, 349, 273]
        ];
        this.getEcharts3('地区日电量统计','map_dltj',data2);
    },
  methods: {
    //母线实时合格率或分线实时合格率
    getEcharts1(name,id,color) {
      var box = echarts.init(
        document.getElementById(id),
        "yundw"
      );
      var option = {
        title: {
          text: name,
          x: "center",
          y: 15,
          textStyle: {
            color: "#555",
            fontSize: "15",
            fontWeight: "normal"
          }
        },
        tooltip: {
          trigger: "item",
          formatter: "{b} : {d}%"
        },

        series: [
          {
            name: name,
            type: "pie",
            radius: ["70%", "80%"],
            center: ["48%", "50%"],
            color: color,
            label: {
              normal: {
                position: "center"
              }
            },
            data: [
              {
                value: 8005,
                name: name,

                label: {
                  normal: {
                    formatter: "{d} %",
                    textStyle: {
                      fontSize: 18,
                      color: color
                    }
                  }
                },

                itemStyle: {
                  normal: {
                    color: color
                  },
                  emphasis: {
                    color: color
                  }
                }
              },
              {
                value: 888,
                name: "不合格率",
                label: {
                  normal: {
                    formatter: "\n合格率",
                    textStyle: {
                      color: "#999",
                      fontSize: 14
                    }
                  }
                },
                itemStyle: {
                  normal: {
                    color: "#ccc"
                  },
                  emphasis: {
                    color: "#ccc"
                  }
                }
              }
            ]
          }
        ]
      };
      box.setOption(option);
    },
    //母线折线或分线折线
    getEcharts2(name,id,color1,color2){
        var box = echarts.init(document.getElementById(id), 'yundw');
        var option = {
            title: {
                text: name,
                x: 'center',
                y: 15,
                textStyle: {
                    color: '#555',
                    fontSize: "15",
                    fontWeight: 'normal'
                }
            },
            tooltip: {
                trigger: 'axis'
            },
            grid: {
                top: 50,
                left: '2%',
                right: '2%',
                bottom: '4%',
                containLabel: true,
            },
            xAxis: [{
                type: 'category',
                boundaryGap: false,
                axisLine: { //坐标轴轴线相关设置。数学上的x轴
                    show: true,
                    lineStyle: {
                        color: '#aaa'
                    },
                },
                axisLabel: { //坐标轴刻度标签的相关设置
                    textStyle: {
                        color: '#666',
                        margin: 15,
                    },
                },
                axisTick: { show: false, },
                data: ['10-1', '10-5', '10-10', '10-15', '10-20', '10-25', '10-30'],
            }],
            yAxis: [{
                type: 'value',
                min: 0,
                max: 100,
                splitNumber: 5,
                splitLine: {
                    show: true,
                    lineStyle: {
                        color: '#aaa'
                    }
                },
                axisLine: { show: false, },
                axisLabel: {
                    margin: 10,
                    textStyle: {
                        color: '#666',

                    },
                },
                axisTick: { show: false, },
            }],
            series: [{
                name: '异常流量',
                type: 'line',
                smooth: true, //是否平滑曲线显示
                // 			symbol:'circle',  // 默认是空心圆（中间是白色的），改成实心圆
                symbolSize: 0,

                lineStyle: {
                    normal: {
                        color: color1   // 线条颜色
                    }
                },
                areaStyle: { //区域填充样式
                    normal: {
                        //线性渐变，前4个参数分别是x0,y0,x2,y2(范围0~1);相当于图形包围盒中的百分比。如果最后一个参数是‘true’，则该四个值是绝对像素位置。
                        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                            { offset: 0, color: color2 },
                            { offset: 0.7, color: color2 }
                        ], false),

                        //shadowColor: 'rgba(53,142,215, 0.9)', //阴影颜色
                        //shadowBlur: 20 //shadowBlur设图形阴影的模糊大小。配合shadowColor,shadowOffsetX/Y, 设置图形的阴影效果。
                    }
                },
                data: [96, 95, 80, 97, 92, 89, 98]
            }]
        }
        box.setOption(option);
    },
    //获取全网日电量统计或地区日电量统计
    getEcharts3(name,id,dataY){
        var box = echarts.init(document.getElementById(id), 'yundw');
        var myData = dataY[0];
        var databeast = {
            1: dataY[1],


        };
        var databeauty = {
            1: dataY[2],


        };
        var timeLineData = [1]
        var option = {
            baseOption: {

                timeline: {
                    show: false,
                    top: 0,
                    data: []
                },
                title: {
                    //   text:'统调上网电量', 
                    textStyle: {
                        color: '#555',
                        fontSize: 16,
                    },

                },
                legend: {
                    data: ['统调上网电量', '非统调上网电量'],
                    top: 5,
                    right: '0%',
                    textStyle: {
                        color: '#555',
                    },
                },
                tooltip: {
                    show: true,
                    trigger: 'axis',
                    formatter: '{b}<br/>{a}: {c}亿千瓦时',
                    axisPointer: {
                        type: 'shadow',
                    }
                },



                grid: [{
                    show: false,
                    left: '4%',
                    top: 40,
                    bottom: 0,
                    containLabel: true,
                    width: '46%',
                }, {
                    show: false,
                    left: '51.5%',
                    top: 60,
                    bottom: 0,
                    width: '0%',
                }, {
                    show: false,
                    right: '4%',
                    top: 40,
                    bottom: 0,
                    containLabel: true,
                    width: '46%',
                },],

                xAxis: [
                    {
                        type: 'value',
                        inverse: true,
                        axisLine: {
                            show: false,
                        },
                        axisTick: {
                            show: false,
                        },
                        position: 'top',
                        axisLabel: {
                            show: true,

                            textStyle: {
                                color: '#555',
                                fontSize: 12,
                            },
                        },
                        splitLine: {
                            show: true,
                            lineStyle: {
                                color: '#ccc',
                                width: 1,
                                type: 'solid',
                            },
                        },
                    }, {
                        gridIndex: 1,
                        show: false,
                    }, {
                        gridIndex: 2,
                        type: 'value',
                        axisLine: {
                            show: false,
                        },
                        axisTick: {
                            show: false,
                        },
                        position: 'top',
                        axisLabel: {
                            show: true,
                            textStyle: {
                                color: '#555',
                                fontSize: 12,
                            },
                        },
                        splitLine: {
                            show: true,
                            lineStyle: {
                                color: '#ccc',
                                width: 1,
                                type: 'solid',
                            },
                        },
                    },],
                yAxis: [{
                    type: 'category',
                    inverse: true,
                    position: 'right',
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        show: false,
                        margin: 8,
                        textStyle: {
                            color: '#9D9EA0',
                            fontSize: 12,
                        },

                    },
                    data: myData,
                }, {
                    gridIndex: 1,
                    type: 'category',
                    inverse: true,
                    position: 'left',
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        show: true,
                        textStyle: {
                            color: '#666',
                            fontSize: 12,
                        },

                    },
                    data: myData.map(function (value) {
                        return {
                            value: value,
                            textStyle: {
                                align: 'center',
                            }
                        }
                    }),
                }, {
                    gridIndex: 2,
                    type: 'category',
                    inverse: true,
                    position: 'left',
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        show: false,
                        textStyle: {
                            color: '#9D9EA0',
                            fontSize: 12,
                        },

                    },
                    data: myData,
                },],
                series: [],

            },

            options: [],


        };

        for (var i = 0; i < timeLineData.length; i++) {
            option.baseOption.timeline.data.push(timeLineData[i]);
            option.options.push({

                series: [{
                    name: '统调上网电量',
                    type: 'bar',
                    barGap: 20,
                    barWidth: 8,
                    label: {
                        normal: {
                            show: false,
                        },
                        emphasis: {
                            show: true,
                            position: 'left',
                            offset: [0, 0],
                            textStyle: {
                                color: '#fff',
                                fontSize: 14,
                            },
                        },
                    },
                    itemStyle: {
                        normal: {
                            color: '#3ab5a0',
                        },
                        emphasis: {
                            color: '#35c7ae',
                        },
                    },
                    data: databeast[timeLineData[i]],
                },


                {
                    name: '非统调上网电量',
                    type: 'bar',
                    barGap: 20,
                    barWidth: 8,
                    xAxisIndex: 2,
                    yAxisIndex: 2,
                    label: {
                        normal: {
                            show: false,
                        },
                        emphasis: {
                            show: true,
                            position: 'right',
                            offset: [0, 0],
                            textStyle: {
                                color: '#fff',
                                fontSize: 14,
                            },
                        },
                    },
                    itemStyle: {
                        normal: {
                            color: '#277ee5',
                        },
                        emphasis: {
                            color: '#308af4',
                        },
                    },
                    data: databeauty[timeLineData[0]],
                }
                ]
            });
        }
        box.setOption(option);
    }
  }
};
</script>
<style>
.yxzl .modular_title{
    text-align:left;
}
</style>



