// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'

import 'element-ui/lib/theme-chalk/index.css'
import {getDate_} from "@/assets/js/getDate.js"
import {
  Message,
  MessageBox,
  Row,
  Col,
  TimePicker,
  DatePicker,
  Pagination,
  Form,
  FormItem,
  Input,
  Select,
  Option,
  Button,
  Tree,
  Dialog,
  Notification,
  Popover,
  Tabs,
  TabPane,
  Progress,
  Scrollbar,
  Collapse,
  CollapseItem, 
  Table,
  TableColumn,
  ColorPicker,
  Checkbox,
  Loading,
  Radio
} from 'element-ui';

Vue.config.productionTip = false
Vue.use(TimePicker)
Vue.use(DatePicker)
Vue.use(Pagination)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Input)
Vue.use(Select)
Vue.use(Option)
Vue.use(Button)
Vue.use(Tree)
Vue.use(Dialog)
Vue.use(Popover)
Vue.use(Row)
Vue.use(Col)
Vue.use(Tabs)
Vue.use(TabPane)
Vue.use(Progress)
Vue.use(Scrollbar)
Vue.use(Collapse)
Vue.use(CollapseItem)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(ColorPicker)
Vue.use(Checkbox)
Vue.use(Loading)
Vue.use(Radio)
Vue.prototype.$msgbox = MessageBox;
Vue.prototype.$alert = MessageBox.alert;
Vue.prototype.$confirm = MessageBox.confirm;
Vue.prototype.$prompt = MessageBox.prompt;
Vue.prototype.$notify = Notification;
Vue.prototype.$message = Message;
Vue.prototype.$getDate = getDate_;

Vue.prototype.$http = axios
Vue.prototype.$http.get("serverconfig.json").then((result) => {
  //用一个全局字段保存ApiUrl  也可以用sessionStorage存储
  axios.defaults.baseURL = result.data.ApiUrl;
}).catch((error) => {
  axios.defaults.baseURL = "http://10.172.246.227:8095"
  axios.defaults.baseURL = 'http://10.42.3.252:8095'
  // axios.defaults.baseURL = 'http://10.42.3.2'
});
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



